/* Nav-Bar Section */

#button {
  background-color: #6415ff;
  color: white;
  width: 118.98px;
  height: 40.8px;
  font-size: 0.875rem;
  outline: none;
}

#button:hover {
  background-color: #4b14b9;
}

#navbar-text {
  color: #243e63;
  font-size: 0.875rem;
  font-weight: 600;
  letter-spacing: 0.025rem;
}

#navbar-text:hover {
  color: var(--color-accent);
  border-bottom: 3px solid #6415ff;
}

#navbar-treact {
  color: #243e63;
  font-size: 1.5rem;
  font-weight: 900;
  letter-spacing: 0.025rem;
}

#navbar-treact:hover {
  color: var(--color-accent);
}

.nav-item-middle {
  margin-left: 1rem;
}
