/*Features Section*/
.features-h5 {
  font-size: 0.9rem;
  font-weight: 700;
  color: var(--color-accent);
}

.features-h2 {
  font-size: 3rem;
  font-weight: 900;
  letter-spacing: 0.025rem;
  margin-bottom: 8rem;
  color: #243e63;
}
.heading-color {
  color: var(--color-accent);
}

.feature-body {
  position: relative;
}

.features-h4 {
  font-size: 1.125rem;
  font-weight: 500;
  color: var(--color-body);
  width: 550px;
  position: absolute;
  top: 50%;
  left: 28%;
}

.feature-inner-h {
  font-size: 1.5rem;
  letter-spacing: 0.025rem;
  font-weight: 700;
  line-height: 1;
  color: #243e6a;
}

.feature-inner-p {
  color: #8c9097;
  width: 211.33px;
  height: 96px;
  line-height: 2;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 2rem;
  margin-bottom: 8rem;
}

.item-1 {
  grid-column: 1/2;
  grid-row: 1/2;
  display: flex;
}

.item-2 {
  grid-column: 2/3;
  grid-row: 1/2;
  display: flex;
}

.item-3 {
  grid-column: 3/4;
  grid-row: 1/2;
  display: flex;
}

.item-4 {
  grid-column: 1/2;
  grid-row: 2/3;
  display: flex;
}

.item-5 {
  grid-column: 2/3;
  grid-row: 2/3;
  display: flex;
}

.item-6 {
  grid-column: 3/4;
  grid-row: 2/3;
  display: flex;
}

.feature-logo {
  width: 33px;
  height: 33px;
  margin-right: 1rem;
}

.feature-circle {
  border-width: 1px;
}
