/* Accordion component*/
.container-accordion {
  width: 896px;
  height: 196px;
  margin-bottom: 10rem;
}

.accordion-item {
  margin: 0.7rem 0;
}

.accordion-title {
  background-color: #edf2f7;
}

.accordion-answer {
  background-color: #edf2f7;
}

.question {
  font-size: 1.5rem;
}

.plus {
  font-size: 1.5rem;
  position: relative;
  left: 800px;
  bottom: 20px;
}

/* Button Switch component */
.btn-switchbox {
  background: transparent;
  border: 1px solid #a8a6a6;
  width: 210px;
  height: 50px;
  border-radius: 50px;
  position: relative;
  left: 500px;
  margin-bottom: 3rem;
}

.button-switch1 {
  width: 100px;
  height: 40px;
  border-radius: 50px;
  background-color: #6415ff;
  color: white;
  position: absolute;
  bottom: 4.5px;
  left: 5px;
  outline: none;
  border: 1px solid lightgray;
}

.button-switch1:hover,
.button-switch2:hover {
  background-color: #4b0ace;
}
.button-switch2 {
  width: 100px;
  height: 40px;
  border-radius: 50px;
  background-color: #6415ff;
  color: white;
  position: absolute;
  bottom: 4.5px;
  left: 105px;
  outline: none;
  border: 1px solid lightgray;
}
