/*Pricing Section*/
#pricing-container {
  margin-top: 3rem;
}

.pricing-h5 {
  font-size: 0.9rem;
  font-weight: 700;
  color: var(--color-accent);
  margin-bottom: 1rem;
  margin-top: 3rem;
}

.pricing-h2 {
  font-size: 3rem;
  font-weight: 900;
  letter-spacing: 0.025rem;
  margin-bottom: 1rem;
  color: #243e6a;
}

.pricing-p {
  width: 576px;
  height: 58.5px;
  font-size: 1.125rem;
  font-weight: 500;
  color: var(--color-body);
  margin-bottom: 1rem;
  position: relative;
  left: 350px;
}

.plan-header-top {
  width: 288px;
  padding: 1rem;
}

.header-inner-top {
  font-size: 3rem;
  font-weight: 700;
}
.header-inner-middle {
  font-size: 1.25rem;
  font-weight: 500;
  color: var(--color-body);
}
.header-inner-bottom {
  font-size: 1.25rem;
  font-weight: 500;
  color: var(--color-body);
}

.plan-header-middle {
  font-size: 1.25rem;
  font-weight: 700;
}

.plan-header-bottom {
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--color-body);
}

.plan-body {
  font-size: 0.75rem;
  font-weight: 600;
  color: var(--color-body);
  margin: 2rem 0;
}

.pricing-plan-container {
  margin-bottom: 3rem;
}
.pricing-plan-item1 {
  background-color: #f7fafc;
}

.pricing-plan1 {
  border: 0.2px solid #e5e7eb;
  box-shadow: 0 0 5px #b9babd;
  margin-right: 6rem;
}

#pricing_plan_1 {
  width: 288px;
}

.pricing-plan2 {
  border: 0.2px solid #e5e7eb;
  box-shadow: 0 0 5px #b9babd;
}

#pricing_plan_2 {
  width: 288px;
}

#button-pricing {
  background-color: #6415ff;
  color: white;
  width: 176.88px;
  height: 56px;
  font-size: 0.875rem;
  font-weight: 700;
  border-radius: 30px;
  margin-bottom: 1.5rem;
}

#button-pricing:hover {
  background-color: #4b14b9;
}
