/*Testimonials Section*/
.testimonial-logo {
  width: 479.33px;
  height: 290.52;
}

.testimonial-h5 {
  font-size: 0.9rem;
  font-weight: 700;
  color: var(--color-accent);
  margin-bottom: 1rem;
}

.testimonial-h2 {
  font-size: 3rem;
  font-weight: 900;
  letter-spacing: 0.025rem;
  margin-bottom: 1rem;
  color: #243e63;
}

.testimonial-p {
  font-size: 1.125rem;
  font-weight: 500;
  color: var(--color-body);
  margin-bottom: 1rem;
}

#carousel-component {
  max-width: 700px;
  height: 400px;
  margin-bottom: 3rem;
}

.testimonial-review {
  max-width: 576px;
  max-height: 150px;
  margin-bottom: 3rem;
  padding: 1rem;
}

#carousel-charlotte {
  position: relative;
  top: 50px;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.testimonial-caption {
}
.testimonial-star,
.testimonial-title,
.testimonial-detail {
  margin: 0.7rem 0.5rem;
}

.testimonial-charlotte,
.testimonial-adam {
  width: 60px;
  height: 60px;
  border-radius: 50%;
}

.button-left {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: #edf2f7;
  margin-right: 1rem;
  outline: none;
  border: none;
}
.button-right {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: #edf2f7;
  margin-left: 1rem;
  outline: none;
  border: none;
}

.button-left:hover,
.button-right:hover {
  background-color: #c2c5c7;
}

.slash {
  color: #edf2f7;
  position: relative;
  top: 10px;
}

.stroke-left {
  position: relative;
  left: 8px;
  color: var(--color-accent);
}
.stroke-right {
  position: relative;
  left: 8px;
  color: var(--color-accent);
}

.testimonial-title {
  font-size: 1.25rem;
  font-size: 700;
  color: #243e63;
}

.testimonial-name {
  font-size: 1.25rem;
  font-size: 700;
  color: #243e63;
}

.testimonial-detail {
  font-size: 1rem;
  font-weight: 500;
  color: #82858a;
  margin-bottom: 1rem;
}

.testimonial-post {
  font-size: 1rem;
  font-weight: 500;
  color: var(--color-body);
  margin-bottom: 1rem;
}
