/*Our Expertise Section*/
.team-logo {
  width: 533.2px;
  height: 516.5;
}

.expertise-flex {
  display: flex;
}

.expertise-h5 {
  font-size: 0.9rem;
  font-weight: 700;
  color: var(--color-accent);
  margin-bottom: 1rem;
}

.expertise-h2 {
  font-size: 3rem;
  font-weight: 900;
  letter-spacing: 0.025rem;
  margin-bottom: 1rem;
  color: #243e63;
}

.expertise-p {
  font-size: 1.125rem;
  font-weight: 500;
  color: var(--color-body);
  margin-bottom: 1rem;
}

.expertise-p1 {
  line-height: 1.625;
  color: #8c9097;
}

.expertise-h4 {
  margin-bottom: 1rem;
  font-size: 1.25rem;
  font-weight: 700;
  margin-left: 1rem;
  color: #243e6a;
}

.suitcase-logo {
  width: 20px;
  height: 20px;
  color: #285e61;
  position: relative;
  top: 5px;
  left: 7px;
}

.dollar-logo {
  width: 20px;
  height: 20px;
  color: #9b282c;
  position: relative;
  top: 5px;
  left: 7px;
}

#expertise-button {
  background-color: #6415ff;
  color: white;
  width: 141.25px;
  height: 44.8px;
  margin-bottom: 7rem;
  font-size: 0.875rem;
}

#expertise-button:hover {
  background-color: #4b14b9;
}

.suitcase-image {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background-color: #81e6d9;
  position: relative;
  bottom: 6px;
}

.dollar-image {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background-color: #feb2b2;
  position: relative;
  bottom: 6px;
}
