/*Main Section*/
#main-img {
  width: 350.4px;
  height: 525.04px;
  position: relative;
  left: 150px;
}

.tick-logo {
  margin-right: 0.5rem;
}

.main-list {
  list-style-type: none;
  margin-bottom: 1rem;
}

.main-right {
  position: relative;
}

.image-box {
  width: 350.4px;
  height: 525.04px;
  background-color: #e2e8f0;
  position: absolute;
  left: 250px;
  top: 75px;
  z-index: -1;
}

.main-h3 {
  font-size: 1.1rem;
  font-weight: 700;
  color: #6b6f74;
  margin-bottom: 1rem;
  text-decoration: none;
  position: relative;
  right: 30px;
}

.main-h2 {
  font-size: 3rem;
  font-weight: 900;
  letter-spacing: 0.025rem;
  margin-bottom: 1rem;
  color: var(--color-secondary);
}

.main-p {
  font-size: 1.125rem;
  font-weight: 500;
  color: var(--color-body);
  margin-bottom: 1rem;
}

#button-main {
  background-color: #6415ff;
  color: white;
  width: 176.88px;
  height: 66px;
  font-size: 1.1rem;
  border-radius: 30px;
}

#button-main:hover {
  background-color: #4b14b9;
}

.quote {
  width: 48px;
  height: 48px;
  color: #f56565;
  position: relative;
  bottom: 205px;
  right: 8px;
  z-index: 2;
}
.main-blockquote {
  width: 384px;
  height: 196px;
  background-color: var(--color-secondary);
  padding: 2.1rem;
  position: relative;
  bottom: 230px;
  left: 15px;
}

.blockquote-text {
  color: white;
  font-size: 0.95rem;
}
