/*Footer Section*/
.footer-container {
  background-color: #f7fafc;
}

.footer-form {
  position: relative;
  top: 45px;
}

.footer-logo > h5 {
  font-size: 1.125rem;
  font-weight: 900;
  letter-spacing: 0.05rem;
  position: relative;
  top: 7px;
}

.footer-form > h4,
.footer-form > p,
.footer-form > button {
  margin-bottom: 2rem;
}

.footer-treact {
  width: 32px;
  height: 32px;
}

.footer-social1 {
  width: 32px;
  height: 32px;
  background-color: black;
  border-radius: 50%;
  padding: 0.4rem;
  margin: 0 0.5rem;
}

.footer-social2 {
  width: 32px;
  height: 32px;
  background-color: black;
  border-radius: 50%;
  padding: 0.4rem;
  margin: 0 0.5rem;
}

.footer-social3 {
  width: 32px;
  height: 32px;
  background-color: black;
  border-radius: 50%;
  padding: 0.4rem;
  margin: 0 0.5rem;
}

.footer-social1:hover,
.footer-social2:hover,
.footer-social3:hover {
  background-color: #474545;
}

.footer-svg-1 {
  position: relative;
  bottom: 3px;
}
.footer-svg-2 {
  position: relative;
  bottom: 3px;
}
.footer-svg-3 {
  position: relative;
  bottom: 3px;
}

.footer-line {
  margin: 3rem 0;
}

.footer-list-heading,
.footer-form > h4 {
  font-size: 1rem;
  font-weight: 700;
  color: #243e63;
}

.footer-list-heading {
  margin: 3rem;
}

.footer-list-body {
  font-size: 0.875rem;
  color: #8c9097;
  list-style-type: none;
  margin: 1rem;
}

.footer-list-body:hover {
  color: #3f3c3c;
  cursor: pointer;
}

.footer-copyright {
  color: #b2bcc7;
}

.footer-bottom {
  margin-bottom: 5rem;
}

#footer-button {
  background-color: #6415ff;
  color: white;
  font-size: 0.875rem;
  margin-left: -1px;
}
