/*Blog Section*/
.blog-grid {
  width: 1162.4px;
  display: grid;
  grid-template-columns: repeat(3, 2fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 3.5rem 0.5rem;
}

.card2 {
  width: 375.46px;
  height: 434px;
}

.card-image2 {
  width: 418.66px;
  height: 320px;
  object-fit: cover;
}

.card-content2 {
  width: 418.66px;
  height: 114px;
  background-color: #edf2f7;
}

.heading-content2 {
  font-size: 0.975rem;
  font-weight: 700;
  color: #243e63;
  margin: 0 1.5rem;
}

.heading-content2:hover {
  color: var(--color-accent);
}

.para-content2 {
  font-size: 0.75rem;
  font-weight: 600;
  color: #8c9097;
  margin: 0.5rem 1.5rem;
}

.grid-link {
  text-decoration: none;
}

.grid-item-1 {
  grid-column: 1/3;
  grid-row: 1/2;
}

.card1 {
  width: 762.93px;
  height: 434px;
}

.card-content1 {
  width: 422.66px;
  height: 434px;
  background-color: #edf2f7;
}

.heading-content1 {
  font-size: 1.5rem;
  font-weight: 700;
  width: 323.46px;
  height: 72px;
  margin: 0 2.5rem;
  color: #243e63;
}

.heading-content1:hover {
  color: var(--color-accent);
}

.para-content1 {
  font-size: 0.875rem;
  font-weight: 600;
  color: #8c9097;
  width: 323.46px;
  height: 91px;
  margin: 0 2.5rem;
}

.heading-person {
  font-size: 0.875rem;
  font-weight: 700;
  color: #243e63;
}

.heading-person:hover {
  color: #243e63;
}
.para-position {
  font-size: 0.75rem;
  font-weight: 500;
  color: #8c9097;
}

.card-image1 {
  width: 422.66px;
  height: 434px;
}

.beach-image {
  width: 422.66px;
  height: 434px;
  object-fit: cover;
}

.adam-image {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  object-fit: cover;
  margin-left: 2rem;
}

.grid-item-2 {
  grid-column: 3/4;
  grid-row: 1/2;
}

.grid-item-3 {
  grid-column: 1/2;
  grid-row: 2/3;
}
.grid-item-4 {
  grid-column: 2/3;
  grid-row: 2/3;
}
.grid-item-5 {
  grid-column: 3/4;
  grid-row: 2/3;
}

.blog-h5 {
  font-size: 0.9rem;
  font-weight: 700;
  color: var(--color-accent);
  margin-bottom: 1rem;
}

.blog-h2 {
  font-size: 3rem;
  font-weight: 900;
  letter-spacing: 0.025rem;
  margin-bottom: 3rem;
  color: #243e63;
}
